import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewerOnWordPress extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Integrating the viewer with WordPress</h1>
                <h4>Add a HTML Block</h4>
                <p>
                    In the WordPress editor click the add button and select Custom HTML in Widgets and drag it onto your page.<br />
                </p>
                <h4>Add the viewer iframe code</h4>
                <ol>
                    <li>Select project specific viewer settings in the Ezyvue Studio settings tab and save the scene.</li>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>In the HTML code section set the desired width and aspect ratio of the viewer window.</li>
                    <li>Select your project in the dropdown.</li>                
                    <li>Click the Copy button and paste into the custom HTML textbox.</li>
                </ol>
            </div>
        );
    }
}

